.schemasContainer {
    border: 1px solid black;
    height: fit-content;
    width: 100%;
    display: flex;
    flex-direction: row;
    gap: 40px;
    overflow-x: auto;
    /* overflow-y: hidden; */
}

.wingContainer {
    display: flex;
    flex-direction: column;
    width: 96px;
    /* background-color: beige; */
}

.noStairsContainer {
    display: flex;
    flex-direction: column;
    width: 62px;
    /* background-color: bisque; */
}

.doubleWingContainer {
    display: flex;
    flex-direction: column;
    width: 158px;
    /* background-color: blanchedalmond; */
}

.block {
    display: flex;
    flex-direction: column;
    /* border: 1px solid rgb(79, 79, 79); */
    width: 100%;
    /* height: 100%; */
    position: relative;
}

.basement {
    width: 100%;
    height: 50px;
    /* background-image: linear-gradient(to left, gray, green); */
    background-size: cover;
    background-image: url('../img/basement.png');
    /* background-color: brown; */
    /* border: 1px solid transparent; */
}

.mainPart {
    display: flex;
    width: 100%;
    height: fit-content;
}

.mainPartReversed {
    flex-direction: row-reverse;
}

.grow {
    flex-grow: 1;
}

.stairs {
    width: 34px;
    background-image: linear-gradient(yellow, red);
}

.flatsContainer {
    display: flex;
    flex-direction: column-reverse;
    width: 62px;
    height: 100%;
}

.flat {
    width: 100%;
    height: 75px;
    background-image: linear-gradient(rgb(251, 249, 246), rgb(185, 185, 185));
    position: relative;
    border: 1px whitesmoke solid;
}

/* Lines for LeftWing start */
.leftWingLineShort {
    position: absolute;
    bottom: 25px;
    left: 50px;
    height: 75px;
    border: 1px solid black;
}

.leftWingLineRotated {
    position: absolute;
    bottom: 23px;
    left: 57px;
    height: 30px;
    border: 1px solid black;
    transform: rotate(28deg);
}

.leftFlatEntranceLine {
    position: absolute;
    width: 20px;
    top: 8px;
    left: 45px;
    border: 1px solid black;
}

