.apartmentDetailsContainer {
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 8px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.apartmentTitle {
    margin-bottom: 20px;
    font-size: 24px;
    font-weight: bold;
    text-align: center;
}

.columns {
    display: flex;
    justify-content: space-between;
    width: 100%;
}

.leftColumn, .rightColumn {
    width: 48%;
}

p {
    margin: 5px 0;
}

b {
    display: inline-block;
    width: 150px;
}

.exportButton {
    margin-top: 20px;
    padding: 10px 20px;
    background-color: #007BFF;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
}

.exportButton:hover {
    background-color: #0056b3;
}
  