.districtButtonsContainer {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.draggableDistrict {
    display: flex;
    align-items: center;
    margin: 5px;
    padding: 10px;
    background-color: #f1f1f1;
    border: 1px solid #ddd;
    border-radius: 5px;
    cursor: move;
}

.districtContent {
    display: flex;
    align-items: center;
}